.cards {
    margin-top: 40px;
    padding-inline: 20px;
}

.cards-title {
    color: white;
    margin-bottom: 20px;
}

.cards-list {
    list-style: none;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
}

.card {
    display: inline-block;
    padding: 5px 10px;
    background-color: rgb(32, 32, 32);
    border: 1px solid rgb(92, 92, 92);
    border-radius: 15px;
}

.card-information {
    margin-bottom: 10px;
}

.card-image {
    height: 200px;
    width: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    margin-bottom: 10px;
}

.card-title {
    color: white;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
}

.card-cost, .card-income {
    color: rgb(185, 185, 185);
}

.card-button {
    width: 100%;
    border: none;
    border-radius: 5px;
    padding: 10px 5px;
}