.user-information {
    background-color: rgb(32, 32, 32);
    border: 1px solid rgb(92, 92, 92);
    border-radius: 15px;
    max-width: 320px;
    padding: 5px 10px;
    margin: 0 auto;
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.user-balance, .user-income, .user-click {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.user-balance, .user-click {
    border-right: 1px solid rgb(133, 133, 133);
}


.user-balance-title, .user-income-title, .user-click-title { 
    color: rgb(185, 185, 185);
    font-size: 12px;
}

.user-balance-counter, .user-income-counter,.user-click-counter {
    color: white;
    font-size: 18px;
    font-weight: bold;
}

.click-area {
    width: 400px;
    height: 400px;
    margin: auto;
    margin-top: 40px;
    cursor: pointer;
}

.coin-image {
    -webkit-user-drag: none;
}

.user-upgrade {
    width: 200px;
    margin: 0 auto;
    margin-top: 10px;
}

.user-upgrade-cost {
    color: rgb(185, 185, 185);
    text-align: center;
}

.user-upgrade-button {
    width: 100%;
    padding: 10px 5px;
    margin-top: 5px;
    color: white;
    background-color: rgb(119, 0, 255);
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    font-size: 16px;
}